/**
 * -
 * @param {*} param0 { id: id, email: , nickname: , user_pic: , username:  }
 * @returns Promise
 */
export const updateUserInfoAPI = ({ id, email, nickname, user_pic, username })=>{
  return request({
    url:'/my/userinfo',
    method:'PUT',
    data:{
      id,
      email,
      nickname,
      user_pic,
      username
    }
  })
}

